@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap);
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
}

body {
	font-family: "Rubik", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #1d2e36;
	color: white;
	max-width: 1100px;
	margin: 0 auto;
}

a {
	color: lightgray;
}

a:visited,
a:active {
	color: #000;
	color: initial;
	text-decoration: none;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App{padding:20px;text-align:left}.won-card{background-color:#7ab382}.lost-card{background-color:#d86e81}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:20px;margin-bottom:20px;border-radius:10px;border:0;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19)}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:20px;margin-bottom:20px;border-radius:10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);border:0}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.team-name-section{grid-column:span 3;justify-self:start;align-self:end}.team-name{font-size:4rem;margin-bottom:20px}.win-loss-section{justify-self:center;padding:40px;text-align:center}.match-detail-section{grid-column:span 4}.more-link{align-self:center;justify-self:center;font-size:1.5rem}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:center;padding:20px}.MatchPage h1{margin:20px auto}

.HomePage .app-name{font-size:3rem;margin:30px auto;justify-self:center;text-align:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage a{color:black}

.TeamTile{height:170px;text-align:center;border-radius:10px;border:0;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);padding:20px;background-color:white}.TeamTile:hover{background-color:lightgray}a:visited,a:active{color:#000;color:initial;text-decoration:none}

